.calendar-day.today {
  background-color: rgba(255, 105, 180, 0.3);
  box-shadow: 0 0 10px rgba(255, 105, 180, 0.5);
}
.calendar-day-number {
  font-weight: 600;
  color: white;
  margin-bottom: 4px;
  text-align: center;
}
.calendar-day-events {
  display: flex;
  flex-direction: column;
  gap: 2px;
  overflow: hidden;
}
.calendar-event {
  font-size: 0.7rem;
  padding: 2px 4px;
  border-radius: 4px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  color: white;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.calendar-event.completed-event {
  position: relative;
}
.calendar-event.completed-event::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  height: 1px;
  background-color: rgba(255, 255, 255, 0.7);
  z-index: 1;
}
.event-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  backdrop-filter: blur(5px);
}
.event-modal {
  background-color: rgba(0, 0, 0, 0.8);
  border-radius: 1rem;
  padding: 2rem;
  width: 90%;
  max-width: 500px;
  box-shadow: 0 0 30px rgba(255, 105, 180, 0.5);
  border: 1px solid rgba(255, 105, 180, 0.5);
  animation: modalFadeIn 0.3s ease-out;
}
@keyframes modalFadeIn {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.event-form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
.form-group label {
  color: #ff69b4;
  font-weight: 500;
}
.event-input,
.event-textarea {
  background-color: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 105, 180, 0.3);
  border-radius: 0.5rem;
  padding: 0.75rem;
  color: white;
  outline: none;
  transition: all 0.3s ease;
}
.event-input:focus,
.event-textarea:focus {
  border-color: rgba(255, 105, 180, 0.7);
  box-shadow: 0 0 10px rgba(255, 105, 180, 0.3);
}
.event-textarea {
  min-height: 100px;
  resize: vertical;
}
.color-options {
  display: flex;
  gap: 0.5rem;
}
.color-option {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.2s ease;
  border: 2px solid transparent;
}
.color-option:hover {
  transform: scale(1.1);
}
.color-option.selected {
  border-color: white;
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.5);
}
.modal-buttons {
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
  margin-top: 1rem;
}
.cancel-btn,
.add-event-btn {
  padding: 0.75rem 1.5rem;
  border-radius: 0.5rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}
.cancel-btn {
  background-color: rgba(255, 255, 255, 0.1);
  color: white;
  border: 1px solid rgba(255, 255, 255, 0.3);
}
.cancel-btn:hover {
  background-color: rgba(255, 255, 255, 0.2);
}
.add-event-btn {
  background: linear-gradient(135deg, #ff69b4, #da70d6);
  color: white;
  border: none;
  box-shadow: 0 2px 8px rgba(255, 105, 180, 0.5);
}
.add-event-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(255, 105, 180, 0.7);
}
.todo-container {
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 0.75rem;
  padding: 1.5rem;
  box-shadow: 0 0 20px rgba(255, 105, 180, 0.2);
  backdrop-filter: blur(5px);
  border: 1px solid rgba(255, 105, 180, 0.3);
  position: relative;
  overflow: hidden;
  animation: fadeIn 0.8s ease-out 0.2s both;
}
.todo-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(45deg,
      rgba(255, 105, 180, 0) 0%,
      rgba(255, 105, 180, 0.05) 50%,
      rgba(255, 105, 180, 0) 100%);
  background-size: 200% 200%;
  animation: todoGlow 8s infinite ease-in-out;
  pointer-events: none;
  z-index: 0;
}
@keyframes todoGlow {
  0% {
    background-position: 0% 0%;
  }
  50% {
    background-position: 100% 100%;
  }
  100% {
    background-position: 0% 0%;
  }
}
.todo-input-container {
  display: flex;
  gap: 0.5rem;
}
.todo-input {
  flex: 1;
  background-color: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 105, 180, 0.3);
  border-radius: 0.5rem;
  padding: 0.75rem;
  color: white;
  outline: none;
  transition: all 0.3s ease;
}
.todo-input:focus {
  border-color: rgba(255, 105, 180, 0.7);
  box-shadow: 0 0 10px rgba(255, 105, 180, 0.3);
}
.todo-add-btn {
  background: linear-gradient(135deg, #ff69b4, #da70d6);
  color: white;
  padding: 0.75rem 1.5rem;
  border-radius: 0.5rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  border: none;
  box-shadow: 0 2px 8px rgba(255, 105, 180, 0.5);
}
.todo-add-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(255, 105, 180, 0.7);
}
.todo-list {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  margin-top: 1.5rem;
  max-height: 400px;
  overflow-y: auto;
  padding-right: 0.5rem;
}
.todo-list::-webkit-scrollbar {
  width: 6px;
}
.todo-list::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.2);
  border-radius: 10px;
}
.todo-list::-webkit-scrollbar-thumb {
  background: rgba(255, 105, 180, 0.5);
  border-radius: 10px;
}
.todo-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgba(255, 255, 255, 0.05);
  border-radius: 0.5rem;
  padding: 0.75rem 1rem;
  transition: all 0.3s ease;
  border-left: 3px solid #ff69b4;
  animation: todoFadeIn 0.5s ease-out;
}
.todo-item.calendar-event-todo {
  background-color: rgba(218, 112, 214, 0.08);
  position: relative;
}
.todo-item.calendar-event-todo::before {
  content: '';
  position: absolute;
  top: -8px;
  left: -8px;
  background-color: rgba(0, 0, 0, 0.7);
  border-radius: 50%;
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 10px;
  box-shadow: 0 0 5px rgba(255, 105, 180, 0.5);
}
@keyframes todoFadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.todo-item:hover {
  background-color: rgba(255, 255, 255, 0.1);
}
.todo-item.completed {
  border-left-color: #4CAF50;
  opacity: 0.7;
}
.todo-checkbox {
  appearance: none;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 2px solid #ff69b4;
  margin-right: 0.75rem;
  cursor: pointer;
  position: relative;
  transition: all 0.2s ease;
}
.todo-checkbox:checked {
  background-color: #4CAF50;
  border-color: #4CAF50;
}
.todo-checkbox:checked::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  font-size: 0.8rem;
}
.todo-text {
  flex: 1;
  color: white;
  transition: all 0.2s ease;
}
.completed .todo-text {
  text-decoration: line-through;
  color: rgba(255, 255, 255, 0.6);
}
.todo-delete-btn {
  background-color: transparent;
  color: rgba(255, 255, 255, 0.5);
  border: none;
  cursor: pointer;
  font-size: 1rem;
  transition: all 0.2s ease;
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}
.todo-delete-btn:hover {
  color: #ff4d4d;
  background-color: rgba(255, 77, 77, 0.1);
}
@keyframes pulse {
  0% {
    opacity: 0.8;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0.8;
  }
}
.text-shadow {
  text-shadow: 0 1px 3px rgba(0, 0, 0, 0.8),
    0 2px 6px rgba(0, 0, 0, 0.6);
}
@media (max-width: 768px) {
  .cosmic-title {
    font-size: 2rem !important;
    padding: 0 1rem;
  }
  .cosmic-subtitle {
    font-size: 0.9rem !important;
    padding: 0 1rem;
  }
  .grid {
    padding: 0 1rem;
  }
}
